@media screen and (max-width: 1100px){
    .category-hub-cont{
        padding: 5rem 8% 4rem;
    }
}

@media screen and (max-width: 800px){
    .category-hub-cont{
        flex-direction: column;
        align-items: center;
        padding: 4rem 1.5rem;
    }
    .category-hub-cont .category-item{
        width: 100%;
        max-width: 400px;
        flex-direction: column;
        margin: 2rem 0;
        padding: 5.5rem 2rem 1rem;
    }
}
.container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5rem 12%
}
.container .text-cont, .container picture{
    width: 47.5%;
}
.container .text-cont h1{
    font-size: 56px;
    margin-bottom: 1rem;
}
.container .text-cont h1 strong{
    font-size: 56px;
    color: hsl(22, 65%, 57%);
}
.container .text-cont p{
    font-size: 15px;
    line-height: 1.6;
    opacity: 75%;
}
.container picture img{
    float: right;
    width: 100%;
    max-width: 500px;
    border-radius: .5rem;
}

@media screen and (max-width: 1100px){
    .container{
        flex-direction: column-reverse;
        padding: 5rem 8%
    }
    .container .text-cont, .container picture{
        width: 100%;
    }
    .container .text-cont{
        text-align: center;
    }
    .container picture img{
        float: unset;
        max-width: unset;
    }
}

@media screen and (max-width: 800px){
    .container .text-cont{
        width: 100%;
    }
    .container .text-cont h1{
        font-size: 32px;
        margin-bottom: 1rem;
    }
    .container .text-cont h1 strong{
        font-size: 32px;
    }
}
footer{
    background-color: black;
    padding: 0 8% 2rem;
}
footer .orange-bar{
    background-color: orange;
    width: 150px;
    height: 2px;
    display: block;
    margin: 0 auto 3rem;
}
footer .footer-top{
    display: flex;
    flex-direction: column;
    align-items: center;
}
footer .footer-top img{
    margin-bottom: 2rem;
}
footer .footer-top .links{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1.5rem 0
}
footer .footer-top .links .navbar-link{
    color: white;
    text-decoration: none;
    margin-bottom: 1.5rem;
    transition: color 200ms ease;
}
footer .footer-top .links .navbar-link:hover{
    color: #D87D4A;
}
footer .footer-text p{
    color: white;
    opacity: .8;
    text-align: center;
    margin-bottom: 2rem;
}
footer .footer-bot p{
    color: white;
    opacity: .8;
    text-align: center;
    margin-bottom: 2rem;
}
footer .footer-bot .icon-cont{
    width: 200px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
}
footer .footer-bot .icon-cont a img{
    transition: filter 200ms ease;
}
footer .footer-bot .icon-cont a:hover img{
    filter: invert(50%) sepia(50%) saturate(2073%) hue-rotate(-20deg) brightness(88%) contrast(92%);
}

@media screen and (min-width: 600px){
    footer .orange-bar{
        margin: 0 0 3rem 0;
    }
    footer .footer-top{
        align-items: flex-start;
    }
    footer .footer-top img{
        margin-bottom: 0;
    }
    footer .footer-top .links{
        flex-direction: row;
        margin: 1.5rem 0;
    }
    footer .footer-top .links .navbar-link{
        color: white;
        margin-bottom: 0;
        margin: 0 1rem 0 0;
    }
    footer .footer-text p{
        text-align: left;
    }
    footer .footer-bot{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    footer .footer-bot .icon-cont{
        margin: 0;
    }
}

@media screen and (min-width: 1100px){
    footer{
        padding: 0 12% 2rem;
    }
    footer .orange-bar{
        margin: 0 0 3rem 0;
    }
    footer .footer-top{
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    footer .footer-text p{
        width: 50%;
        text-align: left;
    }
    footer .footer-bot{
        justify-content: space-between;
    }
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Manrope', sans-serif;
    font-size: 15px;
}

.btn1{
    background-color: hsl(22, 65%, 57%);
    color: white;
    text-decoration: none;
    text-align: center;
    transition: 150ms ease;
    padding: .75rem 3rem;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}
.btn1:hover{
    opacity: .8;
}

.btn2{
    background-color: transparent;
    color: black;
    text-decoration: none;
    text-align: center;
    transition: 150ms ease;
    padding: .75rem 3rem;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    border: black 1px solid;
}
.btn2:hover{
    background-color: black;
    color: white;
}

@media screen and (max-width: 1100px){
    .btn1{
        padding: .75rem 2rem;
    }
    .btn2{
        padding: .75rem 2rem;
    }
}

